@import url(https://fonts.googleapis.com/css2?family=Meow+Script&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.tasks {
	display: flex;
	flex-direction: column;
	align-items: center;
	max-height: 80vh;
	width: 100%;
	max-width: 600px;
	margin: 0 auto;
	background-color: rgba(255,255,255,0.6);
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.tasks__heading {
	font-family: 'Meow Script', cursive;
	font-size: 2.6em;
	padding: 0.4em 0 0.2em;
}

.tasks__separator {
	height: 1px;
	width: 20%;
	max-width: 100px;
	background-color: black;
}

.tasks__list {
    width: 100%;
	margin: .5em;
	overflow-y: auto;
}

.task {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 0 1em;
}

.task__description {
	width: 70%;
	border: none;
	text-align: left;
	background: none;
	font-family: 'Roboto', sans-serif;
    cursor: pointer;
	font-size: 1em;
}

.task__description--done {
	text-decoration: line-through;
}

.task__panel {
	width: 30%;
    max-width: 100px;
	text-align: right;
}

.task__panel--edit,
.task__panel--remove {
	border: none;
	background: none;
	padding: 0.3em;
	font-size: 1.1em;
    cursor: pointer;
}

.new-task {
	position: fixed;
	left: 2em;
	right: 2em;
	bottom: 2em;
	display: flex;
	justify-content: space-between;
	max-width: 600px;
	margin: 0 auto;
	background-color: rgba(255, 255, 255, 0.6);
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
	padding: 0 1em;
}

.new-task--hidden {
	display: none;
}

.new-task__input {
	width: 100%;
	border: none;
	background: none;
    font-size: 1em;
}

.new-task__input:focus {
	outline: none;
}

.new-task__button {
	padding: 0.5em;
	font-size: 1.1em;
	border: none;
	background: none;
	cursor: pointer;
}


html {
	font-size: 62.5%;
}

body {
	background: linear-gradient(to right, pink 0%, pink 50%, paleturquoise 50%, paleturquoise 100%);
	font-family: 'Roboto', sans-serif;
	font-size: 1.6em;
	margin: 2em;
}

body button:focus {
	outline: none;
}
